@import "variables";

.footer {
  background: rgba($black, .6);
  border-top: 1px solid #535353;
  font-size: 0.66rem;
  color: $grey;
  padding: 30px 0;

  .container {
    display: flex;
    align-items: flex-start;

  }

  a {
    color: currentColor;
  }

  .footer-nav {
    li {
      padding: 0 20px 0 0;
